import Vue from 'vue'
import Category from './category/Category.vue'
import Cart from './cart/Cart.vue'
import store from './store'

Vue.config.productionTip = false

new Vue({
  store,
  render: h => h(Cart)
}).$mount('#cart')

const items = [...document.querySelectorAll('.category-list__carousel')]

items.forEach(element => {
  new Vue({
    store,
    render: h => h(Category)
  }).$mount(`#${element.id}`)
})
